import { Component, OnInit } from '@angular/core';
class Chart {
  title;
  type;
  data;
  columnNames;
  options;

  constructor(title, type, data, columnNames, options){
    this.title = title;
    this.type = type;
    this.data = data;
    this.columnNames =  columnNames;
    this.options = options;
  }
}
